@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
   font-family: 'Montserrat', sans-serif;
   color: #000000;
   -webkit-tap-highlight-color: transparent;
}

.App {
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: center;
  align-items: stretch;
  height: 100vh;
  position: relative;
}

a {
  text-decoration: none;
  color: black;
  
}

input, select {
  padding: 8px;
  margin: 2px;
  border-radius: 5px;
  border: solid 1px rgba(0, 0, 0, 0.151);
  width: 200px;
}

select {
  width: 220px;
}

input:focus,
textarea:focus,
select:focus,
input,
textarea,
button,
select,
a,
a:focus,
a:visited, 
a:active {
  -webkit-tap-highlight-color: transparent;
  outline: none;
}

button, .buttonnext-button {
  margin: 10px;
  padding: 10px 20px 10px 20px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
  border-radius: 30px;
  background-color: #373d32;
  border: 0;
  color: #EDEAE4;
  font-size: 4vw;
  cursor: pointer;
}

button:hover, button:active, .buttonnext-button:hover, .buttonnext-button:active {
  box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.25);
  background-color: #67725d;
  transition: all 0.20s;
}

.back-button, .next-button {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 15px;
  margin: 5px 5px 5px 5px;
  padding: 10px 10px 10px 10px;
  width: 90px;
  height: 30px;
  cursor: pointer;
  background-color: #FFFFFF;
  border: solid 2px #373d32;
  color: #373d32;
  font-size: 3vw;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0);
}

.next-button {
  color: #EDEAE4;
  background-color: #373d32;
}

.back-button:hover, .next-button:hover, .back-button:active, .next-button:active {
  background-color: #373d32;
  color: #EDEAE4;
  box-shadow: 0px 0px 0px rgba(0, 0, 0, 0);
  transition: all 0.20s;
}

.next-button:hover, .next-button:active  {
  border: solid 2px #67725d;
  background-color: #67725d;
  color: #EDEAE4;
}

.logout-button {
  width: 200px;
  height: 50px;
  font-size: 5vw;
  border-radius: 10px;
  margin: 20px auto 40px auto;
}

.buttonnext-button {
  margin: 10px;
  padding: 10px 25px 10px 25px;
  width: 150px;
}

.pop-up-box {
  padding: 3px;
  color: #373d32;
}

.light-accent-bg {
  background-color: #EDEAE4;
}

.dark-accent-bg {
  background-color: #DDD8CB;
}

.light-tone-bg {
  background-color: #67725d;
}

.dark-tone-bg {
  background-color: #373d32;
}

.dark-accent-text {
  color: #D4C8BF;
}

.light-accent-text {
  color: #EDEAE4;
}

.light-tone-text {
  color: #67725d;
}

.dark-tone-text {
  color: #373d32;
}

.center-align {
  display: flex;
  justify-content: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  align-items: center;
  margin: auto;
}

.height {
  padding-bottom: 10px;
}

.divLoader {
  font-family: sans-serif;
  text-align: center;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.loader {
  width: 150px;
  height: auto;

  -webkit-animation: blink 1.5s;
  -webkit-animation-iteration-count: infinite;
  -moz-animation: blink 1.5s;
  -moz-animation-iteration-count: infinite;
  -o-animation: blink 1.5s;
  -o-animation-iteration-count: infinite;
}

@media only screen and (min-device-width: 660px) {

button, .buttonnext-button {
  font-size: 14px;
}

.back-button, .next-button {
  font-size: 14px;
}

.logout-button {
  font-size: 18px;
}

.center-align {
  width: 100%;
}

.header {
  position: relative;
  /* height: 16vh; */
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.main {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  width: 100%;
  position: fixed;
  left:0;
  bottom:0;
  right:0;
  height: 12vh;
}

.content {
  flex: 1 0 auto;
}
}
.header-wrapper {
    display: flex;
    align-items: center;
    background-color: #ffffff;
    height: 120px;
    width: 100%;
    position: static;
    overflow: hidden;
    background-image: url(/static/media/feet-bg.76afa961.png);
    background-repeat: no-repeat;
    background-position: -131px -8px;
}

.logo {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    height: 100%;
}

.logo img {
    margin: 10px;
    width: 100px;
    height: auto;
}

.header-main {
    width: 100%;
    text-align: left;
    position: relative;
}

.header-title, .header-title a, .header-title a:visited, .header-title a:active {
    text-decoration: none;
    font-size: 6vw;
    width: 100%;
    font-weight: bold;
    position: absolute;
    margin: 0 0 10px 60px;
}

@media only screen and (min-device-width: 660px) {
    .header-wrapper {
        background-position: -75px 0;
        height: 140px;
    }
    
    .header-title, .header-title a {
        font-size: 28px;
        margin: 0 0 0 120px;
    }

    .logo img {
        margin: 20px;
        width: 150px;
        height: auto;
    }
}
.footer-wrapper {
   display: flex;
   flex-direction: column;
   justify-content: space-around;
   align-items: center;
   background-color: #ffffff;
   padding: 10px;
   height: 96px;
   flex-wrap: wrap;
}

.footer-social,
.footer-copyrights,
.footer-admin,
.footer-social a,
.footer-copyrights a,
.footer-admin a {
   text-align: center;
   color: #000000;
   text-decoration: none;
   font-size: 3vw;
   color: #edeae4;
}

.footer-copyrights {
   display: none;
   text-align: left;
}

.footer-logo {
   display: none;
}

.footer-social-icon {
   height: 20px;
   width: auto;
   padding: 5px;
}

.footer-impressum p {
   font-size: 3.5vw;
   color: gray;
}

.footer-logo-impressum {
   display: flex;
   flex-direction: column-reverse;
   align-items: center;
}

@media only screen and (min-device-width: 660px) {
   .footer-wrapper {
      margin: 0 30px 20px 30px;
      flex-direction: row;
      justify-content: space-around;
      width: 100%;
   }

   .footer-social,
   .footer-admin,
   .footer-social a,
   .footer-admin a {
      font-size: 15px;
   }

   .footer-logo {
      display: block;
      height: 100px;
      width: auto;
      margin-left: -20px;
   }

   .footer-copyrights {
      display: flex;
      flex-wrap: wrap;
      font-size: 12px;
   }

   .footer-social-icon {
      height: 25px;
      width: auto;
      padding: 8px;
   }

   .footer-logo-impressum {
      display: flex;
      flex-direction: row;
      align-items: center;
   }

   .footer-impressum p {
      font-size: inherit;
      padding-right: 30px;
   }
}

@media only screen and (min-device-width: 1200px) {
   .footer-wrapper {
      justify-content: space-between;
      align-items: center;
      width: 90%;
      height: 130px;
   }
   .footer-logo {
      justify-content: center;
      align-items: center;
      margin-left: 0px;
      margin-right: auto;
   }
}

@media only screen and (min-device-height: 1300px) {
   .footer-wrapper {
      position: fixed;
   }
}

.cover-wrapper {
   margin: 0 10px 0 10px;
}

.cover-text-container {
   margin: 0 10px 35px 10px;
   text-align: left;
   font-size: 4vw;
   line-height: 1.4;
}

.cover-intro {
   font-size: 5.5vw;
   margin: 5px auto 20px auto;
}

.cover-title {
   margin: 5px;
   font-size: 4vw;
}

.cover-direction-text {
   font-size: 6vw;
   margin: 15px;
}

.cover-direction-text2 {
   font-size: 3.5vw;
   margin: 0 0 20px 0;
}

.cover-choice-container {
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   margin: 0 10px 0 10px;
}

.cover-img-cover {
   width: 100px;
   margin: 10px;
}

.cover-item-container {
   display: flex;
   flex-direction: column;
   align-items: center;
}

.cover-before-click,
.cover-after-click,
.cover-before-click2,
.cover-after-click2 {
   display: flex;
   flex-direction: row;
   align-items: center;
   justify-content: center;
   width: 150px;
   height: 150px;
   margin: 10px;
   border-radius: 10px;
   box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
   cursor: pointer;
}

.cover-before-click,
.cover-before-click2 {
   background-color: #000000;
}

.cover-before-click2,
.cover-after-click2 {
   width: 270px;
}

.cover-before-click:hover,
.cover-after-click:hover,
.cover-before-click2:hover,
.cover-after-click2:hover {
   background-color:#67725d;
   transition: all 0.25s;
}

.cover-disabled-link {
   pointer-events: none;
}

.tips {
   width: 100%;
}

@media only screen and (min-device-width: 660px) {
   .cover-wrapper {
      margin: 10px 10px 150px 10px;
   }
   .cover-choice-container {
      flex-direction: row;
      justify-content: space-between;
      width: 500px;
      margin: auto auto 5px auto;
   }
   .cover-title {
      font-size: 20px;
      margin: 30px;
   }

   .cover-text-container {
      font-size: 16px;
      width: 80%;
      margin: 20px auto 40px auto;
   }

   .cover-intro {
      font-size: 23px;
   }

   .cover-direction-text {
      font-size: 28px;
      margin: 30px;
   }

   .cover-direction-text2 {
      font-size: 17px;
   }
}

