.category-wrapper {
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   margin: 0 10px 0 10px;
}

.category-items-container {
   display: flex;
   flex-direction: row;
   align-items: center;
   justify-content: center;
}

.category-title {
   font-size: 6vw;
   margin: 20px;
}

.category-img-category-item-box {
   width: 50px;
}

.category-back-next-buttons {
   display: flex;
   justify-content: space-around;
   align-items: center;
   margin: 30px 0 20px 0;
}

.category-type-of-item-container, .category-type-of-item-container-row {
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   flex-wrap: wrap;
   margin: 10px;
}

.category-type-of-item-container-row {
   flex-direction: row;
}

.category-container {
   display: flex;
   flex-direction: column;
   align-items: center;
}

.category-img-text {
   margin: 5px 0 0 0;
}

.category-img-text-s {
   margin: 2px 0 0 0;
   font-size: 10px;
}

.category-type-of-item {
   width: 90px;
   height: 90px;
   margin: 10px 5px 10px 5px;
}

.category-after-click, .category-before-click {
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
   width: 140px;
   height: 140px;
   margin: 5px;
   padding: 5px;
   border-radius: 10px;
   box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
}

.category-before-click {
   background-color: #000000;
}

.category-before-click:hover, .category-before-click:active, .category-deselected:hover {
   background-color: #67725d;
   transition: all 0.25s;
   box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.25);
}

.category-before-click img, .category-after-click img {
   width: 100px;
   margin-bottom: 3px;
}

.category-deselected, .category-selected {
   display: flex;
   align-items: center;
   flex-direction: column;
   justify-content: center;
   border-radius: 10px;
   width: 60px;
   height: 60px;
   border-radius: 10px;
   padding: 10px;
   box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
   cursor: pointer;
}

.category-deselected {
   background-color: #000000;
}

.category-selected {
   box-shadow: 0px 0px 0px rgba(0, 0, 0, 0.25);
}

.category-deselected:hover {
   background-color: #67725d;
}

.category-results-container {
   display: flex;
   justify-content: space-evenly;
}


@media only screen and (min-device-width: 660px) {
   .category-wrapper {
      margin: 10px 10px 150px 30px;
   }
   .category-title {
      font-size: 30px;
      margin: 30px 20px 20px 20px;
   }

   .category-after-click, .category-before-click {
      width: 150px;
      height: 150px;
   }
}
.progressbar-wrapper {
    display: flex;
    margin: 5px 0 5px 0;
    justify-content: center;
    align-items: center;
    font-size: 10px;
    width: 300px;
}

.progressbar {
    display: flex;
    border-radius: 20px;
    width: 170px;
    height: 10px;
}

.progressbar-start {
    width: 30px;
    background-color: #000000;
    border-radius: 20px 0 0 20px;
}

.progressbar-category {
    width: 70px;
}

.progressbar-empty {
    width: 30px;
}

.progressbar-full {
    width: 30px;
    background-color: #000000;
}


@media only screen and (min-device-width: 660px) {
.progressbar-wrapper {
    font-size: 12px;
    width: 360px;
}

