    width: 40px;
}

.progressbar-full {
    width: 40px;
}
}
.itembox-deselected-item-box, .itembox-selected-item-box {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    width: 140px;
    height: 40px; 
    padding: 5px;
    border-radius: 50px;
